footer .social {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
    padding: 0;
    margin-top: 5rem;
}
footer .social .social-title {
    align-self: center;
    margin-bottom: 4.5rem;
    transform: rotate(0.01turn);
}
@media (min-width: 1025px) and (max-width: 1300px) {
