footer .social .social-title h2 {
    text-transform: uppercase;
    font-family: "Cyborg";
    font-size: 3rem;
    font-weight: 900;
    color: #e84393;
    margin: 0;
    opacity: 0.9;
    text-shadow: 3px 3px 0 #e84393, -1px -1px 0 #e84393, 1px -1px 0 #e84393,
        -1px 1px 0 #e84393, 1px 1px 0 #e84393;
}
@supports (-webkit-text-stroke: 0.4px #000) {
    footer .social .social-title h2 {
        -webkit-text-stroke: 0.4px #000;
        -webkit-text-fill-color: #e84393;
    }
}
