    justify-content: center;
    align-items: center;
}
footer .footer p {
    font-size: 1.4rem;
    margin: 0;
    font-family: "Cyborg";
    font-weight: 900;
    color: #e84393;
    text-transform: capitalize;
    padding: 2rem 0;
}
@media (min-width: 1025px) and (max-width: 1300px) {
    footer .footer p {
        font-size: 1.2rem;
    }
}
@media (min-width: 760px) and (max-width: 1024px) {
    footer .footer p {
        font-size: 1.1rem;
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    footer .footer p {
        font-size: 0.9rem;
        text-align: center;
        padding: 0.9rem 0.3rem;
    }
}
@media (max-width: 479px) {
    footer .footer p {
        font-size: 0.9rem;
        text-align: center;
        padding: 0.7rem 0.2rem;
    }
}
footer .footer p span {
    animation: heartBeat 2.5s infinite;
    display: inline-block;
}
@keyframes heartBeat {
    0% {
        transform: scale(1);
    }
    14% {
        transform: scale(1.3);
    }
    28% {
        transform: scale(1);
    }
    42% {
        transform: scale(1.3);
    }
    70% {
        transform: scale(1);
    }
}
