.header .menu-toggle {
    transform: none !important;
}
.header .menu-toggle .nav__checkbox {
    display: none;
}
.header .menu-toggle .nav__checkbox:checked ~ .navigation {
    opacity: 1;
    max-width: 100%;
    width: 100%;
    pointer-events: auto;
}
.header .menu-toggle .nav__checkbox:checked ~ .navigation .large_menu .github {
    animation: zoomIn 0.5s ease forwards;
    opacity: 1;
    transition: opacity 0.5s;
}
.header
    .menu-toggle
    .nav__checkbox:checked
    ~ .navigation
    .large_menu
    .linkedin {
    animation: zoomIn 1s ease forwards;
    opacity: 1;
    transition: opacity 1s;
}
.header .menu-toggle .nav__checkbox:checked ~ .navigation .large_menu .twitter {
    animation: zoomIn 1.5s ease forwards;
    opacity: 1;
    transition: opacity 1.5s;
}
.header
    .menu-toggle
    .nav__checkbox:checked
    ~ .navigation
    .large_menu
    .schedule {
    animation: zoomIn 2s ease forwards;
    opacity: 1;
    transition: opacity 2s;
}
.header .menu-toggle .nav__checkbox:checked + .nav__button .nav__icon {
    background-color: transparent;
}
.header .menu-toggle .nav__checkbox:checked + .nav__button .nav__icon::before {
    transform: rotate(135deg);
    top: 0;
    width: 3.5rem;
}
@media (min-width: 480px) and (max-width: 759px) {
    .header
        .menu-toggle
        .nav__checkbox:checked
        + .nav__button
        .nav__icon::before {
        width: 2.5rem;
        background-color: #e84393;
    }
}
@media (max-width: 479px) {
    .header
        .menu-toggle
        .nav__checkbox:checked
        + .nav__button
        .nav__icon::before {
        width: 2.5rem;
        background-color: #e84393;
    }
}
.header .menu-toggle .nav__checkbox:checked + .nav__button .nav__icon::after {
    transform: rotate(-135deg);
    top: 0;
    width: 3.5rem;
}
@media (min-width: 480px) and (max-width: 759px) {
    .header
        .menu-toggle
        .nav__checkbox:checked
        + .nav__button
        .nav__icon::after {
        width: 2.5rem;
        background-color: #e84393;
    }
}
@media (max-width: 479px) {
    .header
