html,
body {
    width: 100%;
    height: 100%;
    cursor: url("../src/assets/images/arrowPoint.svg") 15 15, move;
}
@media (min-width: 760px) and (max-width: 1024px) {
    html,
    body {
        max-width: 100%;
        max-height: 100%;
    }
}
a[href],
input[type="submit"],
input[type="checkbox"],
input[type="image"],
label[for],
select,
button {
    cursor: url("../src/assets/images/arrowClick.svg") 15 15, move !important;
}
body {
    margin: 0;
    padding: 0;
    background: #050810;
    color: #f5f5f5;
    box-sizing: border-box;
    font-family: "ExoSpace";
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
@font-face {
    font-family: "Cyborg";
    src: local("Cyborg"), url("./assets/fonts/Cyborg.ttf") format("truetype");
}
@font-face {
    font-family: "ExoSpace";
    src: local("ExoSpace"),
        url("./assets/fonts/ExoSpace.ttf") format("truetype");
}
::selection {
    background-color: #e84393;
    color: #f5f5f5;
}
h1,
h2,
h3,
h4,
h5,
h6 {
    font-weight: bold;
    margin: 0;
    padding: 0;
}
h1 {
    font-size: 3.5rem;
    line-height: 60px;
}
h2 {
    font-size: 2.8rem;
    line-height: 48px;
}
h3 {
    font-size: 2.3rem;
    line-height: 34px;
}
h4 {
    font-size: 2rem;
    line-height: 30px;
}
a {
    text-decoration: none;
}
/* Works on Firefox */
* {
    scrollbar-width: thin;
    scrollbar-color: #e84393;
    transform: translate3d(0, 0, 0);
}
/* Works on Chrome, Edge, and Safari */
*::-webkit-scrollbar {
    width: 13px;
}
*::-webkit-scrollbar-track {
    background: #e8fdee;
}
*::-webkit-scrollbar-thumb {
    background-color: #e84393;
    border-radius: 20px;
    border: 2px solid #fdfdfd;
}
