.showroom-section .button-wrap .butt::before {
    content: "";
    border-radius: 7px;
    width: calc(450px + 12px);
    height: calc(80px + 12px);
    border: 6px solid #fd79a8;
    box-shadow: 0 0 60px rgba(20, 233, 86, 0.64);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    opacity: 0;
    transition: all 0.3s ease-in-out 0s;
}
@media (min-width: 1025px) and (max-width: 1300px) {
    .showroom-section .button-wrap .butt::before {
        width: calc(400px + 12px);
        height: calc(70px + 12px);
    }
}
@media (min-width: 760px) and (max-width: 1024px) {
    .showroom-section .button-wrap .butt::before {
        width: calc(340px + 12px);
        height: calc(50px + 12px);
    }
}
@media (min-width: 480px) and (max-width: 759px) {
    .showroom-section .button-wrap .butt::before {
        width: calc(300px + 12px);
        height: calc(40px + 12px);
    }
}
@media (max-width: 479px) {
    .showroom-section .button-wrap .butt::before {
        width: calc(280px + 12px);
        height: calc(35px + 12px);
    }
}
.showroom-section .button-wrap .butt:hover,
.showroom-section .button-wrap .butt:focus {
    transform: translateY(-6px);
}
.showroom-section .button-wrap .butt:hover::before,
.showroom-section .button-wrap .butt:focus::before {
    opacity: 1;
}
.showroom-section .button-wrap .butt::after {
    content: "";
    width: 30px;
    height: 30px;
    border-radius: 7px;
    border: 6px solid #e84393;
    position: absolute;
    z-index: -1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    animation: ring 1.5s infinite;
}
@media (min-width: 1025px) and (max-width: 1300px) {
    .showroom-section .button-wrap .butt::after {
        width: 26px;
        height: 26px;
    }
}
@media (min-width: 760px) and (max-width: 1024px) {
    .showroom-section .button-wrap .butt::after {
