        height: 20px;
    }
}
@media (max-width: 479px) {
    .showroom-section .button-wrap .butt::after {
        width: 19px;
        height: 19px;
    }
}
.showroom-section .button-wrap .butt:hover::after,
.showroom-section .button-wrap .butt:focus::after {
    animation: none;
    display: none;
}
